.AQNextButton {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: #23212B;
  border-right: 5px solid #000;
  cursor: pointer;
  border: none;
  padding: 0px;
  cursor: pointer;
}

.AQDeactive {
  opacity: 0.3;
  cursor: default;
}

@media screen and (max-width: 48rem) {
  .AQDeactive {
    opacity: 0.3;
    cursor: default;
  }
}



